import React from "react";
import Card from "@mui/material/Card";
import { Link } from "react-router-dom";
import {
  CardActionArea,
  CardActions,
  CardHeader,
  CardContent,
  CardMedia,
  Typography,
} from "@mui/material";
import Button from "@mui/material/Button";
import LocalOfferOutlinedIcon from "@mui/icons-material/LocalOfferOutlined";
//import LoadingButton from '@mui/lab/LoadingButton';

function ProductsItem({ item }) {
  // const [loading, setLoading] = useState(false);
  // const handleClick = () =>  {
  //     setLoading(true);
  // }
  //console.log(item);
  return (
    <>
      <Card
        key={item.id}
        className="product-card"
        style={{ textAlign: "center", marginBottom: "60px" }}
      >
        {item.offer && <div className="offer-section">{item.offer}</div>}
        <CardActionArea component={Link} to={`/product/1`} disableRipple>
          <CardHeader title={item.brand} subheader={item.name} />
          <CardMedia
            component="img"
            image={item.img}
            title={item.name}
            height="250"
            alt={item.name}
            className="product-card-img"
          />
          <CardContent>
            <Typography sx={{ fontSize: "14px" }} mt={2}>
              Starting from
            </Typography>
            <Typography className="product-price" mt={2}>
              <span style={{ lineHeight: "28px" }}>BD</span>
              <span style={{ display: "flex" }}>
                <span className="price-label">
                  {item.price.split(".", 1).map((val) => val)}
                </span>
                <span className="price-label-month">
                  <sup>.{item.price.split(".").pop((val) => val)}</sup>
                  <sub>/mo</sub>
                </span>
              </span>
            </Typography>
            <Typography
              sx={{
                fontSize: "14px",
                display: "flex",
                justifyContent: "center",
              }}
              mt={2}
            >
              <LocalOfferOutlinedIcon
                sx={{
                  color: "#4f008c",
                  fontSize: 16,
                  transform: "rotate(90deg)",
                  marginRight: "5px",
                }}
              />
              Full price:
              {item.discount_price && (
                <strike style={{ color: "#ff375e", margin: "0 0 0 5px" }}>
                  {item.discount_price}BD
                </strike>
              )}
              <span style={{ fontWeight: "bold", margin: "0 5px" }}>
                BD {item.full_price}
              </span>
              VAT inclusive
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions sx={{ padding: "0 40px 40px" }}>
          <Button
            component={Link}
            to={`/product/1`}
            className="primary-button"
            disableRipple
          >
            {item.cta}
          </Button>
        </CardActions>
      </Card>
    </>
  );
}

export default ProductsItem;
