import React, { useEffect, useState } from "react";
import { getProdcuts } from "../services/api";
import { getCategories, getBrands } from "../services/api";
import Grid from "@mui/material/Grid";
import ProductsItem from "./ProductsItem";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import CircularProgress from "@mui/material/CircularProgress";
import Box from "@mui/material/Box";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
//import Button from "@mui/material/Button";

export default function Products() {
  const cardPerRow = 3;
  const [products, setProducts] = useState([]);
  const [category, setCategory] = useState([]);
  const [selectCategory, setSelectedCategory] = useState("Smartphones");
  const [brand, setBrand] = useState("All Brands");
  const [selectBrand, setSelectedBrands] = useState([]);
  const [loading, setLoading] = useState(false);
  const [sort, setSorting] = useState("sort");
  const [filteredProducts, setfilteredProducts] = useState([]);
  const [offer, setOffer] = useState("offers");
  const [offerValue, setOfferValue] = useState(0);
  const [filterData, setFilterData] = useState([]);
  const [next, setNext] = useState(cardPerRow);

  const getProductsData = () => {
    setLoading(true);
    getProdcuts()
      .then((res) => {
        setProducts(res.data.devices);
        setLoading(false);
        //filterProducts(res.data.devices);
        selectedProduct(res.data.devices);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const getCategoriesData = () => {
    //getCategories()
    setCategory(getCategories());
    setSelectedBrands(getBrands());
    setBrand("All Brands");
  };

  useEffect(() => {
    getProductsData();
    getCategoriesData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const selectedProduct = (product) => {
    const defaultfilteredProduct = product.filter(
      (device) => device.category === selectCategory
    );
    setfilteredProducts(defaultfilteredProduct);
    setFilterData(defaultfilteredProduct);

    filterProducts(product, selectCategory);
  };

  const handleChange = (event) => {
    setSelectedCategory(event.target.value);

    const filteredProductsData = products.filter(
      (device) => device.category === event.target.value
    );
    setfilteredProducts(filteredProductsData);
    setBrand("All Brands");
    setOffer("offers");
    setFilterData(filteredProductsData);
    filterProducts(filteredProductsData, event.target.value);
  };

  const filterProducts = (filteredData, type) => {
    const filteredOfferLength = filteredData
      .filter((val) => val.category === type)
      .filter((val) => val.offer !== "");
    setOfferValue(filteredOfferLength.length);
  };

  const handleOfferChange = (event) => {
    setOffer(event.target.value);

    if (event.target.value === "offer") {
      const filteredOffer = filteredProducts.filter(
        (device) => device.offer !== ""
      );
      setfilteredProducts(filteredOffer);
    } else {
      setfilteredProducts(filterData);
    }
  };

  const handleSortChange = (event) => {
    setSorting(event.target.value);
    if (event.target.value !== "sort") {
      sortFunction(filteredProducts, event.target.value);
    }
    // else {
    //   setfilteredProducts(filterData);
    // }
  };

  const brandFilter = (results, type) => {
    const filteredBrand = results
      .filter((val) => val.category === selectCategory)
      .filter((val) => val.brand === type);
    setfilteredProducts(filteredBrand);
  };

  const handleBrandChange = (event) => {
    setBrand(event.target.value);
    brandFilter(products, event.target.value);
    if (event.target.value === "All Brands") {
      setOffer("offers");
      return setfilteredProducts(filterData);
    }
  };

  const sortFunction = (results, type) => {
    if (type === "ascending") {
      results.sort((a, b) => (a.name > b.name ? 1 : -1));
    } else if (type === "descending") {
      results.sort((a, b) => (a.name > b.name ? -1 : 1));
    }
    //setProducts(results);
  };

  const handleMoreCard = () => {
    setNext(next + cardPerRow);
  };

  return (
    <>
      {loading ? (
        <Box sx={{ display: "flex", justifyContent: "center" }}>
          <CircularProgress />
        </Box>
      ) : (
        <>
          <Grid container spacing={2} sx={{ padding: "20px 0 60px" }}>
            <Grid item xs={12} md={4}>
              <FormControl sx={{ m: 1, minWidth: { xs: "100%", md: "400px" } }}>
                <Select
                  id="simple-select"
                  value={selectCategory}
                  label=""
                  onChange={handleChange}
                  IconComponent={KeyboardArrowDownIcon}
                >
                  {category?.map((val, index) => (
                    <MenuItem key={index} value={val}>
                      {val}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid
              item
              md={8}
              sx={{
                display: { xs: "none", md: "flex" },
                justifyContent: "flex-end",
              }}
            >
              <FormControl sx={{ m: 1, minWidth: 170 }}>
                <Select
                  value={brand}
                  displayEmpty
                  onChange={handleBrandChange}
                  inputProps={{ "aria-label": "Without label" }}
                  IconComponent={KeyboardArrowDownIcon}
                >
                  {selectBrand.map((item, index) => (
                    <MenuItem key={index} value={item}>
                      {item}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              <FormControl sx={{ m: 1, minWidth: 170 }}>
                <Select
                  value={offer}
                  onChange={handleOfferChange}
                  displayEmpty
                  inputProps={{ "aria-label": "Without label" }}
                  IconComponent={KeyboardArrowDownIcon}
                >
                  <MenuItem value="offers">Offers</MenuItem>
                  <MenuItem value="offer">Offer ({offerValue})</MenuItem>
                </Select>
              </FormControl>

              <FormControl sx={{ m: 1, minWidth: 170 }}>
                <Select
                  value={sort}
                  onChange={handleSortChange}
                  displayEmpty
                  inputProps={{ "aria-label": "Without label" }}
                  IconComponent={KeyboardArrowDownIcon}
                >
                  <MenuItem value="sort">Sorting</MenuItem>
                  <MenuItem value="ascending">Name (A-Z)</MenuItem>
                  <MenuItem value="descending">Name (Z-A)</MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </Grid>

          <Grid container spacing={3} py={3}>
            {filteredProducts?.map((item, index) => (
              <Grid
                item
                xs={12}
                sm={6}
                md={4}
                key={index}
                sx={{ position: "relative" }}
              >
                <ProductsItem item={item} />
              </Grid>
            ))}
            {/* {filteredProducts?.slice(0, next)?.map((item, index) => (
              <Grid
                item
                xs={12}
                sm={6}
                md={4}
                key={index}
                sx={{ position: "relative" }}
              >
                <ProductsItem item={item} />
              </Grid>
            ))} */}
          </Grid>
          {/* {next < filteredProducts?.length && (
            <div style={{ display: "flex", justifyContent: "center" }}>
              <Button
                onClick={handleMoreCard}
                className="secondary-button"
                disableRipple
              >
                Load more
              </Button>
            </div>
          )} */}
        </>
      )}
    </>
  );
}
