import axios from "axios";

const baseURL = `https://mocki.io/v1`;

export const getProdcuts = () => {
  return axios.get(`${baseURL}/ae832675-0711-48a0-8665-a186cd0e8489`);
};

const categories = ["Smartphones", "tablets", "accessories"];
const brands = ["All Brands", "Apple", "Samsung", "Vivo"];

export const getCategories = () => {
  return categories;
};

export const getBrands = () => {
  return brands;
};

export const getSingleProdcut = () => {
  return axios.get(`${baseURL}/2f527fd4-4597-4b49-8080-f0b77321ed2b`);
};

