import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { remove } from "../store/CartSlice";
import { useNavigate } from "react-router-dom";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import DeleteOutlineOutlinedIcon from "@mui/icons-material/DeleteOutlineOutlined";
import ShoppingCartOutlinedIcon from "@mui/icons-material/ShoppingCartOutlined";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import Divider from "@mui/material/Divider";
import NavigateBeforeIcon from "@mui/icons-material/NavigateBefore";

function Cart() {
  const navigate = useNavigate();
  const cartProducts = useSelector((state) => state.cart);
  const dispatch = useDispatch();
  const [totalUpfront, setTotalUpfront] = useState(0);
  const [vatAmount, setVatAmount] = useState(0);

  const removeCart = (id) => {
    dispatch(remove(id));
  };

  const getCartData = () => {
    const upfront = cartProducts.reduce(
      (prevVal, currentVal) => prevVal + currentVal.price,
      0
    );
    setTotalUpfront(upfront);
    setVatAmount(upfront / 10);
  };

  const sumTotal = () => {
    return (totalUpfront + vatAmount).toFixed(3);
  };

  useEffect(() => {
    if (cartProducts.length > 0) {
      getCartData();
    }
  }, [removeCart]); // eslint-disable-line react-hooks/exhaustive-deps

  const styles = {
    listStyle: {
      width: "100%",
      bgcolor: "background.paper",
    },
    summaryBox: {
      borderRadius: "20px",
      boxShadow: "0 2px 4px 0 rgba(142,154,160,.08)",
      border: "solid 1px rgba(142,154,160,.28)",
      backgroundColor: "#fff",
    },
    summaryTitle: {
      fontSize: "19px",
      fontWeight: "bold",
      letterSpacing: ".5px",
      paddingLeft: "14px",
    },
  };

  return (
    <>
      {cartProducts.length > 0 ? (
        <>
          <Grid container spacing={2}>
            <Grid item xs={8}>
              {cartProducts.map((product, index) => (
                <Card
                  style={styles.summaryBox}
                  variant="outlined"
                  sx={{ display: "flex", m: 4, p: 2 }}
                  key={index}
                >
                  <Box
                    sx={{
                      display: "flex",
                      width: 1,
                      flexWrap: {
                        xs: "wrap",
                        md: "nowrap",
                        justifyContent: "space-between",
                      },
                    }}
                  >
                    <Box sx={{ display: "flex" }}>
                      <CardMedia
                        sx={{
                          height: 100,
                          width: 150,
                          //flex: { xs: "0 0 auto", md: "1 0 auto" },
                          backgroundSize: "contain",
                        }}
                        image={product.img}
                        title={product.name}
                      />

                      <CardContent>
                        <Typography gutterBottom variant="h5">
                          {product.name}
                        </Typography>
                        <Typography gutterBottom variant="h6">
                          {product.storage}, {product.color}
                        </Typography>
                      </CardContent>
                    </Box>

                    <Box>
                      <Typography
                        gutterBottom
                        variant="span"
                        sx={{
                          color: "#ff375e",
                          textDecoration: "line-through",
                        }}
                      >
                        {product.price}
                      </Typography>
                      <Typography
                        gutterBottom
                        variant="h6"
                        sx={{ fontSize: "30px" }}
                      >
                        <sup style={{ fontSize: "12px" }}>BD</sup>{" "}
                        {product.price}
                      </Typography>
                    </Box>
                    <Box>
                      BD {product.price}
                      <CardActions>
                        <DeleteOutlineOutlinedIcon
                          sx={{ cursor: "pointer" }}
                          onClick={() => removeCart(index)}
                        />
                      </CardActions>
                    </Box>
                  </Box>
                </Card>
              ))}
            </Grid>
            <Grid item xs={4}>
              <Box sx={styles.summaryBox}>
                <Accordion>
                  <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                    sx={{ padding: "18px 30px", borderRadius: "20px 0" }}
                  >
                    <ShoppingCartOutlinedIcon
                      sx={{ color: "#4f008c", fontSize: "30px" }}
                    />
                    <Typography style={styles.summaryTitle}>
                      Order summary
                    </Typography>
                  </AccordionSummary>
                  <AccordionDetails sx={{ borderTop: "1px solid #D2D7D9" }}>
                    <List
                      sx={styles.listStyle}
                      component="nav"
                      aria-label="folders"
                    >
                      <ListItem button>
                        <ListItemText primary="Total Upfront" />
                        BD {totalUpfront}
                      </ListItem>
                      <Divider />
                      <ListItem button divider>
                        <ListItemText
                          primary="Total Device Amount (10% VAT)"
                          secondary="Why do I need to pay this VAT?"
                        />
                        BD {vatAmount}
                      </ListItem>
                      <ListItem button divider>
                        <ListItemText primary="Discount" />
                        BD 100
                      </ListItem>
                      <ListItem button divider>
                        <ListItemText primary="Delivery" />
                        BD 0.00
                      </ListItem>
                      <ListItem button>
                        <ListItemText primary="Total Monthly Commitment" />
                        BD 100
                      </ListItem>
                    </List>
                  </AccordionDetails>
                </Accordion>
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "space-between",
                    borderBottom: "1px solid #ddd",
                    padding: "22px 60px 22px 30px",
                    alignItems: "center",
                  }}
                >
                  <Typography variant="body1">
                    <strong style={{ display: "block" }}>Total</strong> VAT
                    INCLUSIVE BD
                  </Typography>
                  <Typography
                    gutterBottom
                    variant="h2"
                    sx={{ fontSize: "48px" }}
                  >
                    {sumTotal()}
                  </Typography>
                </Box>
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "space-between",
                    padding: "30px 30px 24px",
                  }}
                >
                  <Button
                    disableRipple
                    variant="contained"
                    className="secondary-button"
                    fullWidth
                  >
                    Checkout
                  </Button>
                </Box>
              </Box>
            </Grid>
          </Grid>
          <Button onClick={() => navigate(-1)} sx={{ color: "#ff375e" }}>
            <NavigateBeforeIcon sx={{ fontSize: "30px" }} /> CONTINUE SHOPPING
          </Button>
        </>
      ) : (
        <p> Cart is empty</p>
      )}
    </>
  );
}
export default Cart;
