import React, { useEffect, useState } from "react";
import { getSingleProdcut } from "../services/api";
//import { useParams } from "react-router";
import { Link } from "react-router-dom";
import Button from "@mui/material/Button";
import CircularProgress from "@mui/material/CircularProgress";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import Typography from "@mui/material/Typography";
import ProductFeatures from "./ProductFeatures";
import DoneIcon from "@mui/icons-material/Done";
import Stack from "@mui/material/Stack";
import ImageGallery from "react-image-gallery";
import Dialog from "@mui/material/Dialog";
import CloseIcon from "@mui/icons-material/Close";
import IconButton from "@mui/material/IconButton";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import { useDispatch } from "react-redux";
import { add } from "../store/CartSlice";
import Snackbar from "@mui/material/Snackbar";
import Alert from "@mui/material/Alert";

function ProductDetail() {
  const [productDetail, setProductsDetail] = useState();
  const [loading, setLoading] = useState(false);
  const [value, setValue] = useState("");
  const [colorValue, setColorValue] = useState("");
  const [colorName, setColorName] = useState("");
  const [planStatus, showPlanStatus] = useState(true);
  const [images, setImages] = useState([]);
  const [planPrice, setPlanPrice] = useState("");
  const [devicePlan, setDevicePlan] = useState("");
  const [openChangePlan, setChangePlanOpen] = useState(false);
  const [openChangeNumber, setChangeNumberOpen] = useState(false);
  const [installment, setInstallment] = useState("");
  const [scrollTo, setScrollTo] = useState(false);
  const [toastOpen, setToastOpen] = useState(false);

  const priceResponse = ["Pay in installments", "Full price"];
  const planResponse = ["New Mobile Plan", "Existing stc plan"];
  const installmentResponse = [
    "18 MONTHS DEVICE INSTALLMENT",
    "24 MONTHS DEVICE INSTALLMENT",
  ];

  //const { id } = useParams();

  // const getProductData = async () => {
  //     setLoading(true);
  //     id && await getSingleProdcut(id).then((res) => {
  //         console.log(res.data);
  //         setProductsDetail(res.data);
  //         setLoading(false);
  //     }).catch((error) => {
  //         console.log(error);
  //     });
  // };

  const imageSlider = (images) => {
    var img = [];
    for (let i = 0; i < images.length; i++) {
      img.push({ original: images[i], thumbnail: images[i] });
    }
    setImages(img);
  };

  const handleRadioChange = (event) => {
    setValue(event.target.value);
  };

  const handleColorChange = (event) => {
    setColorValue(event.target.value);
    const filteredColor = productDetail.Color.filter(
      (color) => color.code === event.target.value
    );
    setColorName(filteredColor[0].name);
  };

  const handlePriceChange = (event) => {
    setPlanPrice(event.target.value);
    if (event.target.value === "Full price") {
      showPlanStatus(false);
    } else {
      showPlanStatus(true);
    }
  };

  const handleInstallChange = (event) => {
    setInstallment(event.target.value);
  };

  const handlePlanChange = (event) => {
    setDevicePlan(event.target.value);
    // if (event.target.value === "Existing stc plan") {
    //   window.location.href =
    //     "https://shop.stc.com.bh/index.php?route=account/login&referrer=product%2Faddplantodevice";
    // }
  };

  const getProductData = async () => {
    setLoading(true);
    await getSingleProdcut()
      .then((res) => {
        setProductsDetail(res.data.devices[0]);
        setValue(res.data.devices[0].storage[0]);
        setColorValue(res.data.devices[0].Color[0].code);
        setColorName(res.data.devices[0].Color[0].name);
        setLoading(false);
        imageSlider(res.data.devices[0].img);
        setPlanPrice(priceResponse[0]);
        setDevicePlan(planResponse[0]);
        setInstallment(installmentResponse[0]);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const newState = (product) => {
    const updateState = {
      img: product?.img[0],
      name: product?.name,
      storage: value,
      color: colorName,
      price: product?.full_price,
    };
    dispatch(add(updateState));
  };
  //setSelectedProduct(newState);

  const handleClickOpen = () => {
    setChangePlanOpen(true);
  };

  const handleClose = () => {
    setChangePlanOpen(false);
  };

  const handleNumberOpen = () => {
    setChangeNumberOpen(true);
  };

  const handleNumberClose = () => {
    setChangeNumberOpen(false);
  };

  const handleScroll = (event) => {
    if (event.currentTarget.scrollY >= 1000) {
      setScrollTo(true);
    } else {
      setScrollTo(false);
    }
  };

  useEffect(() => {
    getProductData();
    window.addEventListener("scroll", handleScroll);
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  const fullPriceLabel = (val) => {
    return (
      <div className="full-price-label">
        {val}
        <div>
          BD <strike>462.210</strike> 423.700
        </div>
        <span>VAT inclusive</span>
      </div>
    );
  };

  const installmentLabel = (val) => {
    return (
      <div className="installment-label">
        BD 16
        <sup>
          <strong>.300</strong>/mo
        </sup>
        <div className="installment-label-val">{val}</div>
      </div>
    );
  };

  const dispatch = useDispatch();

  const addToCart = (product) => {
    newState(product);
    setToastOpen(true);
  };

  const handleToastClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setToastOpen(false);
  };

  return (
    <>
      {loading ? (
        <Box sx={{ display: "flex", justifyContent: "center" }}>
          <CircularProgress />
        </Box>
      ) : (
        <>
          <Grid container spacing={2} className="product-detail-container">
            <Grid
              item={true}
              md={6}
              sx={{ position: "relative" }}
              className={scrollTo ? "img-container" : ""}
            >
              <Link to={`/`} style={{ display: "flex", margin: "15px" }}>
                Back
              </Link>

              <ImageGallery
                items={images}
                showFullscreenButton={false}
                showPlayButton={false}
                thumbnailPosition={"left"}
                showNav={false}
                infinite={false}
                slideDuration={0}
              />
            </Grid>
            <Grid item={true} md={6}>
              <Typography
                sx={{
                  fontSize: "16px",
                  marginBottom: "15px",
                  textTransform: "capitalize",
                }}
              >
                {productDetail?.brand}
              </Typography>
              <Typography
                variant="h2"
                sx={{
                  fontSize: "38px",
                  fontWeight: "bold",
                  marginBottom: "15px",
                }}
              >
                {productDetail?.name}
              </Typography>

              <Box
                className="your-selection"
                sx={{
                  border: 1,
                  borderRadius: 5,
                  borderColor: "#d2d2d7",
                  marginBottom: 3,
                }}
              >
                <Stack
                  direction="row"
                  justifyContent="space-between"
                  alignItems="center"
                  spacing={2}
                  sx={{ padding: "15px 30px", borderBottom: "1px solid #eee" }}
                >
                  <Typography variant="h3">Your selection</Typography>
                  <div
                    style={{
                      display: "flex",
                      fontSize: "12px",
                      alignItems: "center",
                    }}
                  >
                    <DoneIcon sx={{ color: "#00c48c" }} />
                    <strong>In Stock.</strong> <span>Available now.</span>
                  </div>
                </Stack>
                <FormControl>
                  <Grid container item sx={{ padding: "15px 30px" }}>
                    <Grid item md={6}>
                      <FormLabel id="demo-row-radio-buttons-group-label">
                        Storage
                      </FormLabel>
                      <RadioGroup
                        row
                        aria-labelledby="demo-row-radio-buttons-group-label"
                        name="row-radio-buttons-group"
                        value={value}
                        onChange={handleRadioChange}
                        className="storage-radio"
                        sx={{ mt: 2 }}
                      >
                        {productDetail?.storage.map((val, index) => (
                          <React.Fragment key={index}>
                            <Box
                              sx={{
                                display: "flex",
                                gap: 2,
                              }}
                            >
                              <FormControlLabel
                                value={val}
                                control={<Radio />}
                                label={val}
                              />
                              {val === value && <DoneIcon />}
                            </Box>
                          </React.Fragment>
                        ))}
                      </RadioGroup>
                    </Grid>
                    <Grid item md={6}>
                      <FormLabel id="demo-row-radio-buttons-grou">
                        Color: {colorName}
                      </FormLabel>
                      <RadioGroup
                        row
                        aria-labelledby="demo-row-radio-buttons-group"
                        name="row-radio-buttons-group"
                        value={colorValue}
                        onChange={handleColorChange}
                        className="color-radio"
                        sx={{ mt: 2 }}
                      >
                        {productDetail?.Color.map((val, index) => (
                          <FormControlLabel
                            value={val.code}
                            key={index}
                            control={
                              <Radio
                                disableRipple
                                sx={{
                                  "& .css-hyxlzm": {
                                    backgroundColor: val.code,
                                    display: "inline-block",
                                    width: "20px",
                                    height: "20px",
                                    borderRadius: "50%",
                                    backgroundClip: "content-box",
                                    padding: "1px",
                                  },
                                  "& .MuiSvgIcon-root": {
                                    display: "none",
                                  },
                                }}
                              />
                            }
                            label={val.code}
                          />
                        ))}
                      </RadioGroup>
                    </Grid>
                  </Grid>
                </FormControl>
              </Box>

              <Box
                className="device-payment"
                sx={{
                  border: 1,
                  borderRadius: 5,
                  borderColor: "#d2d2d7",
                  marginBottom: 3,
                }}
              >
                <Typography
                  variant="h3"
                  sx={{ padding: "15px 30px", borderBottom: "1px solid #eee" }}
                >
                  Device payment terms
                </Typography>
                <FormControl sx={{ width: "100%" }}>
                  <RadioGroup
                    row
                    aria-labelledby="demo-row-radio-buttons-group-label"
                    name="row-radio-buttons-group"
                    className="custom-radio price-radio"
                    onChange={handlePriceChange}
                    value={planPrice}
                  >
                    <Grid container sx={{ padding: "15px 30px 0" }} spacing={2}>
                      {priceResponse?.map((val, index) => (
                        <React.Fragment key={index}>
                          <Grid
                            item
                            xs={6}
                            sx={{
                              display: "flex",
                              gap: 2,
                              width: "50%",
                              position: "relative",
                            }}
                          >
                            <FormControlLabel
                              value={val}
                              control={<Radio />}
                              label={
                                val === "Full price" ? fullPriceLabel(val) : val
                              }
                            />
                            {val === planPrice && <DoneIcon />}
                          </Grid>
                        </React.Fragment>
                      ))}
                    </Grid>
                  </RadioGroup>

                  {planStatus && (
                    <RadioGroup
                      aria-labelledby="demo-row-radio-buttons-group-label"
                      name="column-radio-buttons-group"
                      className="custom-radio installment-radio"
                      onChange={handleInstallChange}
                      value={installment}
                    >
                      <Grid container sx={{ padding: "15px 30px" }}>
                        {installmentResponse?.map((val, index) => (
                          <React.Fragment key={index}>
                            <Grid
                              item
                              xs={12}
                              sx={{
                                display: "flex",
                                position: "relative",
                              }}
                            >
                              <FormControlLabel
                                value={val}
                                control={<Radio />}
                                label={installmentLabel(val)}
                              />
                              {val === installment && <DoneIcon />}
                            </Grid>
                          </React.Fragment>
                        ))}
                      </Grid>
                    </RadioGroup>
                  )}
                </FormControl>
              </Box>

              {planStatus && (
                <Box
                  className="choose-plan"
                  sx={{
                    border: 1,
                    borderRadius: 5,
                    borderColor: "#d2d2d7",
                    marginBottom: 3,
                  }}
                >
                  <Typography
                    variant="h3"
                    sx={{
                      padding: "15px 30px",
                      borderBottom: "1px solid #eee",
                    }}
                  >
                    Choose a plan with your device
                  </Typography>
                  <FormControl sx={{ width: "100%" }}>
                    <RadioGroup
                      row
                      aria-labelledby="demo-row-radio-buttons-group-label"
                      name="row-radio-buttons-group"
                      className="custom-radio plan-radio"
                      onChange={handlePlanChange}
                      value={devicePlan}
                    >
                      <Grid container sx={{ padding: "15px 30px" }} spacing={2}>
                        {planResponse?.map((val, index) => (
                          <React.Fragment key={index}>
                            <Grid
                              item
                              xs={6}
                              sx={{
                                display: "flex",
                                gap: 2,
                                position: "relative",
                              }}
                            >
                              <FormControlLabel
                                value={val}
                                control={<Radio />}
                                label={val}
                              />
                              {val === devicePlan && <DoneIcon />}
                            </Grid>
                          </React.Fragment>
                        ))}
                      </Grid>
                    </RadioGroup>
                  </FormControl>
                  <Box
                    sx={{
                      borderRadius: 5,
                      border: "1px solid #d2d2d7",
                      margin: "0 30px 30px",
                    }}
                  >
                    <Typography
                      sx={{
                        backgroundColor: "#4f008c",
                        borderRadius: "10px 10px 0 0",
                        padding: "8px 30px",
                        color: "#fff",
                        fontWeight: "bold",
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                      }}
                    >
                      Recommended plan for you
                      <Button
                        onClick={handleClickOpen}
                        sx={{ color: "#ff375e" }}
                      >
                        CHANGE PLAN
                        <ArrowForwardIosIcon sx={{ fontSize: 20 }} />
                      </Button>
                    </Typography>
                    <Box sx={{ padding: "15px 30px" }}>
                      <Box
                        sx={{
                          display: "flex",
                          justifyContent: "space-between",
                          marginBottom: "10px",
                          flexWrap: "wrap",
                        }}
                      >
                        <div style={{ display: "flex" }}>
                          <Typography className="plan-name">
                            Postpaid 9
                          </Typography>
                          <div className="plan-tag">Online Exclusive</div>
                        </div>
                        <div className="plan-info">
                          BD 9<sup style={{ fontSize: "12px" }}>.000/mo</sup>
                        </div>
                      </Box>
                      <Typography sx={{ fontSize: "14px" }}>
                        No contract ,
                        <strong>16 GB data + 500 local minutes.</strong>
                      </Typography>
                      <Box
                        sx={{
                          display: "flex",
                          justifyContent: "space-between",
                          border: "1px solid #4f008c",
                          padding: "20px",
                          borderRadius: "8px",
                          marginTop: "20px",
                        }}
                      >
                        <div>
                          <Typography sx={{ fontSize: "16px" }}>
                            Selected number
                          </Typography>
                          <Typography
                            sx={{ fontSize: "24px", fontWeight: "bold" }}
                          >
                            33226837
                          </Typography>
                        </div>
                        <div
                          style={{ display: "flex", alignItems: "flex-end" }}
                        >
                          <Typography
                            sx={{
                              color: "#ff375e",
                              fontWeight: "bold",
                              textTransform: "uppercase",
                              display: "flex",
                              cursor: "pointer",
                            }}
                            onClick={handleNumberOpen}
                          >
                            Change number
                            <ArrowForwardIosIcon sx={{ fontSize: 20 }} />
                          </Typography>
                        </div>
                      </Box>
                    </Box>
                  </Box>
                </Box>
              )}

              <Box
                sx={{
                  borderRadius: 5,
                  backgroundColor: "#F6F2F9",
                  padding: "24px 32px",
                  display: "flex",
                  justifyContent: "space-between",
                  flexWrap: "wrap",
                  marginBottom: "60px",
                }}
              >
                <div>
                  <Typography
                    sx={{
                      color: "#4f008c",
                      fontSize: "12px",
                      fontWeight: "bold",
                    }}
                  >
                    TOTAL PLAN + DEVICE
                  </Typography>
                  <Typography sx={{ fontSize: "38px", fontWeight: "bold" }}>
                    BD {productDetail?.full_price}
                    <sup style={{ fontSize: "12px", fontWeight: "normal" }}>
                      .113 VAT inclusive
                    </sup>
                  </Typography>
                </div>
                <div>
                  <Button
                    disableRipple
                    variant="contained"
                    className="secondary-button"
                    onClick={() => addToCart(productDetail)}
                  >
                    Add to cart
                  </Button>
                </div>
              </Box>
            </Grid>
          </Grid>
          <Snackbar
            open={toastOpen}
            autoHideDuration={3000}
            message="Added to Cart"
            onClose={handleToastClose}
            anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
          >
            <Alert
              severity="success"
              variant="filled"
              action={
                <IconButton
                  aria-label="close"
                  color="inherit"
                  size="small"
                  onClick={() => {
                    setToastOpen(false);
                  }}
                >
                  <CloseIcon fontSize="inherit" />
                </IconButton>
              }
            >
              Added to Cart
            </Alert>
          </Snackbar>
          <Dialog fullScreen open={openChangePlan} onClose={handleClose}>
            <Typography
              sx={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                padding: "20px",
                fontSize: "24px",
                fontWeight: "bold",
                borderBottom: "1px solid #dde0e2",
              }}
            >
              Pick your perfect plan
              <IconButton
                color="inherit"
                onClick={handleClose}
                aria-label="close"
              >
                <CloseIcon sx={{ fontSize: "50px" }} />
              </IconButton>
            </Typography>
            <Typography sx={{ textAlign: "center", padding: "20px" }}>
              Plan cards come here
            </Typography>
          </Dialog>
          <Dialog
            fullScreen
            open={openChangeNumber}
            onClose={handleNumberClose}
          >
            <Typography
              sx={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                padding: "20px",
                fontSize: "24px",
                fontWeight: "bold",
                borderBottom: "1px solid #dde0e2",
              }}
            >
              Pick Your Perfect Number
              <IconButton
                color="inherit"
                onClick={handleNumberClose}
                aria-label="close"
              >
                <CloseIcon sx={{ fontSize: "50px" }} />
              </IconButton>
            </Typography>
            <Typography sx={{ textAlign: "center", padding: "20px" }}>
              We picked this fancy number for you
            </Typography>
          </Dialog>
          <ProductFeatures />
        </>
      )}
    </>
  );
}

export default ProductDetail;
